* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.container {
    min-width: 200px;
    max-width: 1024px;
}

.form {
    &__block { 
        display: flex; 
        flex-direction: column;
        width: 286px;
        padding: 10px 26px 16px 20px;
        background-color: #4a90e2;
        color: #fff;
    }
    &__title {
        margin-bottom: 15px;
        font-family: 'Open Sans', sans-serif;
        font-size: 18px;
        font-weight: 600;
    }
    &__subtitle {
        order: 1;
        font-family: 'Open Sans', sans-serif;
        font-size: 10px;
        font-weight: 600;
    }
    &__input-item {
        position: relative;
        width: 240px;
        
        & img {
            position: absolute;
            top: 13px;
            right: 10px;
            pointer-events: none;
        }
    }
    &__input {
        display: block;
        width: 100%;
        margin-bottom: 15px;
        padding: 12px 15px;
        font-family: 'Open Sans', sans-serif;
        font-size: 14px;
        color: #9b9b9b;
        border-radius: 2px;
        border: none;
    }
    &__date + label  {
        content: url('../calendar.svg');
    }
    &__submit {
        font-family: 'Open Sans';
        font-size: 20px;
        color: #fff;
        background-color: #f5a623;
        text-transform: uppercase;
    }
    &__input-block {

    }
}  

@media (min-width: 768px) {
    br {
        display: none;
    }
    .form {
        &__input-block {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
        }
        &__input-item {
            width: 190px;
        }
        &__block {
            width: 440px;
            padding: 10px 20px 16px 20px;
        }
        &__title {
            margin-bottom: 8px;
        }
        &__subtitle {
            order: 0;
            margin-bottom: 15px;
        }
        &__submit {
            max-width: 100%;
            margin-bottom: 0;
        }
    }
}

@media (min-width: 1024px) {
    .form {
        &__input-block {
            flex-wrap: nowrap;
        }
        &__block {
            width: 768px;
            padding: 3px 20px 15px 20px;
        }
        &__input-item {
            width: 220px;
        }
        &__input {
        margin-bottom: 0;
        }
        &__title {
            font-size: 24px;
            margin-bottom: 10px;
        }
        &__submit {
            max-width: 240px;
            padding: 7px 15px;
            margin-left: 9px;
        }
    }
}

@media (min-width: 1200px) {
    .form {
        &__block {
            width: 1024px;
            flex-wrap: wrap;
            justify-content: space-between;
            flex-direction: row;
        }
        &__title {
            order: 1;
            width: 100%;
        }
        &__subtitle {
            order: 2;
            max-width: 245px;
            margin-bottom: 0;
        }
        &__input-block {
            width: 69%;
            justify-content: unset;
            order: 2;
        }
        &__input-item {
            width: 200px;
            margin-right: 20px;
        }
        &__input {
            width: auto;
        }
        &__submit {
            width: 100%;
            margin-left: 0;
        }
    }
}